import { useEffect, useState } from "react";
import api from "@/services/api";
import { useRouter, useParams } from "next/navigation";
import "../../globals.css";

// Interface para o Questionário
interface Questionario {
    id: number;
    titulo: string;
}

// Interface para a Pergunta
interface Pergunta {
    id: number;
    enunciado: string;
    tipos: string;
    questionarioId: number; // Chave para filtrar as perguntas
}

export default function ViewQuestionario() {
    const [questionario, setQuestionario] = useState<Questionario | null>(null);
    const [perguntas, setPerguntas] = useState<Pergunta[]>([]);
    const router = useRouter();
    const { id } = useParams();
    const questionarioId = Number(id);

    useEffect(() => {
        if (!questionarioId) return;

        // Função para carregar o título do questionário
        const loadQuestionario = async () => {
            try {
                const { data } = await api.get<Questionario>(`/questionarios/${questionarioId}`);
                setQuestionario(data);
            } catch (error) {
                console.error("Erro ao carregar o questionário:", error);
                alert("Questionário não encontrado");
                router.push("/questionarios");
            }
        };

        // Função para carregar e filtrar as perguntas
        const loadPerguntas = async () => {
            try {
                // Presume-se que GET /perguntas retorna todas as perguntas
                const response = await api.get<Pergunta[]>('/perguntas');
                // Filtra as perguntas pelo ID do questionário atual
                const filteredPerguntas = response.data.filter(p => p.questionarioId === questionarioId);
                setPerguntas(filteredPerguntas);
            } catch (error) {
                console.error("Erro ao carregar as perguntas:", error);
            }
        };

        loadQuestionario();
        loadPerguntas();

    }, [questionarioId, router]);

    if (!questionario) {
        return <div>Carregando...</div>;
    }

    return (
        <div className="View">
            <div className="title-container">
                <h1>{questionario.titulo}</h1>
                <button
                    onClick={() => router.push(`/questionarios/update/${questionarioId}`)}
                    className="btn-secondary"
                >
                    Editar Título
                </button>
            </div>

            <hr className="editor-separator" />

            <h3>Perguntas do Questionário</h3>
            <div className="perguntas-list">
                {perguntas.length > 0 ? (
                    perguntas.map(pergunta => (
                        <div key={pergunta.id} className="pergunta-item">
                            <p><strong>Enunciado:</strong> {pergunta.enunciado}</p>
                            <p><strong>Tipo:</strong> {pergunta.tipos}</p>
                        </div>
                    ))
                ) : (
                    <p>Nenhuma pergunta encontrada para este questionário.</p>
                )}
            </div>

            <div className="button-group">
                <button
                    onClick={() => alert('Funcionalidade a ser implementada!')} // Ex: router.push(`/perguntas/create?questionarioId=${id}`)
                    className="btn-primary"
                >
                    Adicionar Nova Pergunta
                </button>
                <button
                    type="button"
                    onClick={() => router.push("/questionarios")}
                    className="btn-secondary"
                >
                    Voltar para a Lista
                </button>
            </div>
        </div>
    );
}